import React from "react";
import { useCMS } from "../../contexts/CMSContext";
import "./programef.css";

const ProgramSectionef: React.FC = () => {
  const { displayData } = useCMS();
  const data = displayData.pages.page6.programSectionEFContent;

  return (
    <section className="programef-program-section">
      <div className="programef-program-container">

        {/* LEFT IMAGE */}
        <div className="programef-program-image">
          <img
            className="desktop-image"
            src={data.images.desktop}
            alt={data.images.alt}
            onError={(e) => (e.currentTarget.style.display = 'none')}
          />

          <img
            className="mobile-image"
            src={data.images.mobile}
            alt={data.images.alt}
            onError={(e) => (e.currentTarget.style.display = 'none')}
          />

          {/* Mobile Overlay Content */}
          <div className="programef-mobile-overlay">
            <div className="programef-mobile-header">
              <h2 className="programef-mobile-title">{data.title}</h2>
            </div>
            <div className="programef-mobile-links">
              <ul className="programef-mobile-links-list">
                {data.links.map((link: any, linkIndex: number) => {
                  const hasLink = link.href && link.href !== "#" && link.href !== "";
                  return (
                    <li key={linkIndex} className="programef-mobile-link-item">
                      <a
                        href={hasLink ? link.href : "javascript:void(0)"}
                        className="programef-mobile-link"
                        onClick={(e) => !hasLink && e.preventDefault()}
                        style={{ cursor: hasLink ? 'pointer' : 'default' }}
                      >
                        {link.label}
                      </a>
                    </li>
                  );
                })}
              </ul>
            </div>
          </div>
        </div>

        {/* RIGHT CONTENT */}
        <div className="programef-program-content">
          <div className="programef-program-hero-text">
            <h2 className="programef-program-title">{data.title}</h2>
            <div className="programef-program-underline" />
          </div>

          <ul className="programef-program-links">
            {data.links.map((link: any) => {
              const hasLink = link.href && link.href !== "#" && link.href !== "";
              return (
                <li key={link.id}>
                  <a
                    href={hasLink ? link.href : "javascript:void(0)"}
                    onClick={(e) => !hasLink && e.preventDefault()}
                    style={{ cursor: hasLink ? 'pointer' : 'default' }}
                  >
                    {link.label}
                  </a>
                </li>
              );
            })}
          </ul>

        </div>
      </div>
    </section>
  );
};

export default ProgramSectionef;
