import React from "react"
import "./chuongtrinh.css"
import { useCMS } from "../../contexts/CMSContext"

const Page6a: React.FC = () => {
  const { displayData } = useCMS()
  const content = displayData.pages.page6.chuyenKhoaATyDamContent

  const imgOnLeft = (content.imagePosition || 'left') === 'left'

  return (
    <section className="chuongtrinh-section">
      <div className="chuongtrinh-container">

        {/* IMAGE */}
        <div className="chuongtrinh-image" style={{ order: imgOnLeft ? 1 : 2 }}>
          <img
            src={content.image.src}
            alt={content.image.alt}
            className="chuongtrinh-image-content"
            onError={(e) => (e.currentTarget.style.display = 'none')}
          />
        </div>

        {/* CONTENT */}
        <div className="chuongtrinh-content" style={{ order: imgOnLeft ? 2 : 1 }}>

          <div className="chuongtrinh-header">
            <h2 className="chuongtrinh-title">
              {content.title}
            </h2>
            <div className="chuongtrinh-underline" />
          </div>

          <div className="chuongtrinh-meta">
            <p>{content.meta.duration}</p>
            {content.meta.schedule.map((line: string, i: number) => (
              <p key={i}>{line}</p>
            ))}
          </div>

          <div className="chuongtrinh-curriculum">
            <div className="curriculum-grid">

              <div className="curriculum-column">
                <h3 className="curriculum-heading">
                  {content.curriculum.kosa.heading}
                </h3>
                <ul>
                  {content.curriculum.kosa.items.map(
                    (item: string, i: number) => (
                      <li key={i}>{item}</li>
                    )
                  )}
                </ul>
              </div>

              <div className="curriculum-column">
                <h3 className="curriculum-heading">
                  {content.curriculum.abhidhamma.heading}
                </h3>
                <ul>
                  {content.curriculum.abhidhamma.items.map(
                    (item: string, i: number) => (
                      <li key={i}>{item}</li>
                    )
                  )}
                </ul>
              </div>

            </div>
          </div>

        </div>
      </div>
    </section>
  )
}

export default Page6a
